.pane {
  background-color: #f4f4f4;
  border-radius: 10px;
  width: 80%;
  margin-bottom: 10px;
  padding: 10px;
  box-shadow: 0 2px 4px #0000001a;
}

.pane p {
  color: #333;
  margin: 5px 0;
  font-size: 16px;
  font-weight: bold;
}

.pane input[type="text"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: calc(100% - 20px);
  margin-bottom: 10px;
  padding: 8px;
}

.pane button {
  color: #333;
  cursor: pointer;
  background-color: #ddd;
  border: none;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 8px 16px;
  transition: background-color .3s;
  transform: translateY(30%);
}

.pane button:hover {
  background-color: #f5f5f5;
}

.pane button span {
  filter: blur(.5px);
}

.pane.frontend {
  background-color: #add8e6;
}

.pane.backend {
  background-color: #90ee90;
}

.pane.ux {
  background-color: #ffb6c1;
}
